
import './menu.scss'

export default function Menu({open, isOpen}) {
  return (
    <div className={'menu ' + (open && "active")} >
        <ul onClick={()=>isOpen(!open)}>
            <li >  
                <a href='#intro'>Home</a>
            </li>
            <li> 
                <a href='#portfolio'>Portfolio</a>
            </li>
            <li> 
                <a href='#works'>Works</a>
            </li>
            <li> 
                <a href='#testimonials'>Testimonials</a>
            </li> 
            <li> 
                <a href='#contact'>Contact</a>
            </li>
        </ul>
    </div>
  )
}

